html, body, #root, .App {
  min-height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  overflow-x: hidden;
}
.App {
  text-align: center;
  width: 100%;
  max-width: 100%;
  height: 100%;
  display: flex;
  flex: 1;
  flex-direction: column;
  font-family: "Open Sans", sans-serif;
  background-color: #f0f0f0;
}
body {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.grecaptcha-badge {
  visibility: hidden;
}
